#banner {
    width: 100%;
    height: 100vh;
    /* background: url('D:\Nơi lưu Projects Visual Studio Code\Hoc_js\benhcaytrong\public\webImage\bn.jpg') top center / cover no-repeat; */
    margin-top: calc(0px - var(--height-header));
}

.content{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: var(--main-margin);
    transform: translateZ(10px);
}


.content_introduce{
    /* font-family: 'EB Garamond', serif; */
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: white;
    line-height: 2.5rem;
    text-align: center;
}


.content_heading{
    font-size: 21.6px;
    font-weight: 100;
}


.content_logo_1{
    font-size: 96.3px;
    line-height: 130px;
    display: inline-block;
    margin: 30px 0px;
    font-family: 'lobster', 'sans-serif';
    color: white!important;
    animation: right_appear 1s linear forwards;
}

@keyframes right_appear {
    from{
      transform: translateX(10%);
      opacity: 0;
    }
    to{
      transform: translateX(0);
      opacity: 1;
    }
  }


.content_logo_3{
    font-size: 96.3px;
    line-height: 100px;
    margin: 30px 0px;
    font-family: 'lobster', 'sans-serif';
    display: inline-block;
    animation: left_appear 1s linear forwards;
}

@keyframes left_appear {
    from{
      transform: translateX(-20%);
      opacity: 0;
    }
    to{
      transform: translateX(0);
      opacity: 1;
    }
  }


   
.content_learn-more-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 151px;
  height: 50px;
  background-size: 300% 300%;
  backdrop-filter: blur(1rem);
  border-radius: 5rem;
  transition: 0.5s;
  animation: gradient_301 5s ease infinite;
  border: double 4px transparent;
  background-image: linear-gradient(#212121, #212121),  linear-gradient(137.48deg, #ffdb3b 10%,#FE53BB 45%, #8F51EA 67%, #0044ff 87%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  margin: 0 auto;
}

#container-stars {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: 0.5s;
  backdrop-filter: blur(1rem);
  border-radius: 5rem;
}

strong {
  z-index: 2;
  font-family: 'Avalors Personal Use';
  font-size: 13.5px;
  letter-spacing: 5px;
  color: #FFFFFF;
  text-shadow: 0 0 4px white;
}

#glow {
  position: absolute;
  display: flex;
  width: 12rem;
}

.circle {
  width: 100%;
  height: 30px;
  filter: blur(2rem);
  animation: pulse_3011 4s infinite;
  z-index: -1;
}

.circle:nth-of-type(1) {
  background: rgba(254, 83, 186, 0.636);
}

.circle:nth-of-type(2) {
  background: rgba(142, 81, 234, 0.704);
}

.content_learn-more-btn:hover #container-stars {
  z-index: 1;
  background-color: #212121;
}

.content_learn-more-btn:hover {
  transform: scale(1.1)
}

.content_learn-more-btn:active {
  border: double 4px #FE53BB;
  background-origin: border-box;
  background-clip: content-box, border-box;
  animation: none;
}

.content_learn-more-btn:active .circle {
  background: #FE53BB;
}

#stars {
  position: relative;
  background: transparent;
  width: 200rem;
  height: 200rem;
}

#stars::after {
  content: "";
  position: absolute;
  top: -10rem;
  left: -100rem;
  width: 100%;
  height: 100%;
  animation: animStarRotate 90s linear infinite;
}

#stars::after {
  background-image: radial-gradient(#ffffff 1px, transparent 1%);
  background-size: 50px 50px;
}

#stars::before {
  content: "";
  position: absolute;
  top: 0;
  left: -50%;
  width: 170%;
  height: 500%;
  animation: animStar 60s linear infinite;
}

#stars::before {
  background-image: radial-gradient(#ffffff 1px, transparent 1%);
  background-size: 50px 50px;
  opacity: 0.5;
}

@keyframes animStar {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-135rem);
  }
}

@keyframes animStarRotate {
  from {
    transform: rotate(360deg);
  }

  to {
    transform: rotate(0);
  }
}

@keyframes gradient_301 {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes pulse_3011 {
  0% {
    transform: scale(0.75);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.75);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}



